// import { Children } from "react";



const Modal = ({ children, handleModalClose, open, title }) => {


    return (
        <div className={`modal fade ${open ? 'show': ''}`} tabindex="-1" style={{ display: open ? 'block': 'none'}}>
            <div className="modal-dialog">
                <div className="modal-content">
                    <div className="modal-header">
                        <h5 className="modal-title">{title}</h5>
                        <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close" onClick={() => handleModalClose(false)}></button>
                    </div>
                    <div className="modal-body">
                        {children}
                    </div>
                    <div className="modal-footer">
                        <button type="button" className="btn btn-secondary" data-bs-dismiss="modal" onClick={() => handleModalClose(false)}>Close</button>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Modal;