import './UsersList.scss'

import { useEffect, useState } from 'react'
import { useRef } from 'react';



import request from '../../services/http'
import Modal from '../../components/Modal';


const UsersList = () => {

    // Adding New User

    const name = useRef(null);
    const job = useRef(null);


    const handleSubmit = (e) =>{
        e.preventDefault()
        // console.log(name.current.value, lastname.current.value, password.current.value, email.current.value);

        request.post('/users', {
            name: name.current.value,
            job: job.current.value,
        })
        .then((response)=>{
            console.log(response);
            name.current.value = ''
            job.current.value = ''
        })
        .catch((err)=>{
            console.log(err);
        })
        
        

    }

    // UserList

    const [userlist, setUserList] = useState({
        isFetched: true,
        data: [],
        error: null,
    })

    // Single User API

    const [single, setSingle] = useState({
        isFetched: true,
        data: {},
        error: null,
    })

    const [modalState, setModalState] = useState(false)

    const [newmodalState, setNewModalState] = useState(false)

    useEffect(() => {
        request.get('users?page=1')
            .then((response) => {
                // console.log(response);
                setUserList({
                    isFetched: true,
                    data: response.data.data,
                    error: false,
                })
            })
            .catch((err) => {
                setUserList({
                    isFetched: true,
                    data: [],
                    error: true,
                })
            })
    }, [])

    const getSingleUser = (id) =>{

        console.log(id)
        
        setModalState(true)
        request.get(`/users/${id}`)
        .then((response) =>{
            console.log(response);
            setSingle({
                isFetched: true,
                data: response.data,
                error: false,
            })
        })
        .catch((err) =>{
            console.log(err);
            setSingle({
                isFetched: true,
                data: {},
                error: true,
            })
        })
    }

    console.log(single);

    return (
        <div>

            

            

            <div style={{background: 'blue', color: 'white', padding: '15px 0px'}} className="mb-5"><h1 className="container">Users List</h1></div>
            <div className="container">

            <button className="btn btn-success mb-3" style={{width: '100%'}} onClick={() => setNewModalState(true)} >ADD NEW USER</button>

            <Modal open={newmodalState} handleModalClose={setNewModalState} title='New User' >
                    
                     <form onSubmit={handleSubmit} className="d-flex flex-column">
                        <input type="text" ref={name} className="mb-3 form-control" name="" id="" placeholder="Name..." />
                        <input type="text" ref={job} className="mb-3 form-control" name="" id="" placeholder="Job..." />
                        <button type="submit" class="btn btn-primary mb-3">Add</button>
                    </form>  
                    
            </Modal>

                <div className="d-flex flex-wrap justify-content-between align-items-center">
                    {
                        userlist.data.map((item) => {
                            // console.log(item.id);
                            return(
                                <div className="card" style={{maxWidth: "300px", width: '100%', border: '1px solid #f2f2f2', borderRadius: '5px'}} className="mb-3">
                                <img src={item.avatar} className="card-img-top" alt="..."/>
                                    <div className="card-body ">
                                        <h5 className="card-title">{item.first_name} {item.last_name}</h5>
                                        <p className="card-text">{item.email}</p>
                                        <button className="btn btn-outline-primary" style={{width: '100%'}} onClick={() => getSingleUser(item.id)}>Personal Information</button>
                                    </div>
                            </div>
                            )
                        })
                    }
                </div>
            </div>

            <Modal open={modalState} handleModalClose={setModalState} title='Single user'>
                    
                     <div>
                         <h2>User ID: {single?.data?.data?.id}</h2> 
                        <h2>Name: {single?.data?.data?.first_name}</h2> 
                        <h2>Last Name: {single?.data?.data?.last_name}</h2> 

                        <h2>Email: {single?.data?.data?.email}</h2>  
                    </div>  
                    
            </Modal>

            

        </div>
    )
}

export default UsersList